import React from 'react';

const Tr = React.createClass({
  rmAdmin: function () {
    const {action}=this.props;
    const header = '?';
    const body = `${this.props.data.name}?,!`;
    const id = this.props.id;
    const callback = this.props.callback;
    action.showConfirm(header, body, id);
  },
  editAdmin: function () {
    const {showEditAdmin}=this.props.action;
    const data = this.props.data;
    showEditAdmin(data);
  },
  render: function () {
    const {data}=this.props;
    return (
      <tr>
        <td>{data.index}</td>
        <td>{data.code}</td>
        <td>{data.name}</td>
        <td>{data.academy}</td>
        <td>{data.role}</td>
        <td>{data.email}</td>
        <td>{data.phone}</td>
        <td className="opr-admin">
          <span className="edit-admin" onClick={this.editAdmin}></span>
          <span className="edit-admin" onClick={()=>{this.props.action.showChangeAdminPass(data.id)}}></span>
          <span className="text-danger" onClick={this.rmAdmin}></span>
        </td>
      </tr>
    )
  }
});


export  default Tr;



